import {login,register} from '@/api/user.js'
const Cookie = process.client ? require('js-cookie') : undefined
export default {
  middleware:'notauthenticate',
  name:'LoginIndex',
  computed:{
  isLogin(){
      return this.$route.name==='login'
      }
  },
  data(){
    return{
      user:{
        username:'',
        email:'',
        password:'',
      },
      errors:{}
    }
  },
  mounted() {
    window.addEventListener("scroll", ()=>{this.handleScroll}, true);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll, true);
  },
  methods:{
    handleScroll() {
     let scrollTop = window.pageXOffset||document.documentElement.scrollTop||document.body.scrollTop; //表示获取router-view上一级的scrollTop
      if (scrollTop > 170) {
        this.fixedDiv = true;
      } else {
        this.fixedDiv = false;
      }
    },
    async onSubmit(){
      try{
        const {data}=this.isLogin?await login({
        user:this.user
      }):await register({
        user:this.user
      })
      console.log(data)
      // 保存用户登录状态
      this.$store.commit('setUser',data.user)
      Cookie.set('user', data.user) // saving token in cookie for server rendering
      this.$router.push('/')
      }catch(err){
        this.errors=err.response.data.errors
        // console.log('error', this.errors)
        // console.dir(error)
      }
     
    }
  }
}

